@tailwind base;
@tailwind components;
@tailwind utilities;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
      Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    line-height: 1.5;
  }
  
  .main-section {
    padding: 10px;
  }
  
  header {
    padding: 10px 0 20px 0;
  }
  
  .theme-dropdown {
    max-width: 150px;
    margin: auto;
    color: #000;
  }
  
  .theme-dropdown span {
    display: block;
    margin: 0 0 5px 0;
  }
  
  .content p {
    padding: 10px;
  }